                </li>
                <li>
                    <div><img src="@/assets/img/catalog/money.svg" alt="money">{{ item.childPrice }} сом</div>
                    Бала билет
                </li>
                <li>
                    <div><img src="@/assets/img/catalog/time.svg" alt="time">{{ item.time }} саат</div>
                </li>
            </ul>
            <p>{{ item.descr.slice(0, 100) + "..." }}</p>
