        </div>
    </div>
    </div>
    <div v-else>
        <h2>Кечириниз! Сиз каалаган турлар жок</h2>
    </div>
</template>

<script>
import { onMounted } from '@vue/runtime-core';
import { useStore } from 'vuex';

export default {
    setup() {
        const store = useStore();
        onMounted(() => {
            store.commit("getCategoryFilter", "ВСЕ");
        })

        return {
            store,
        }
    }

}
</script>

